// Ported from perlin reference implementation (https://cs.nyu.edu/~perlin/noise/) by @alvrs

export function perlin(_x: i32, _y: i32, _z: i32, denom: i32): f64 {
  // Convert fraction into f64
  let x: f64 = f64(_x) / f64(denom);
  let y: f64 = f64(_y) / f64(denom);
  let z: f64 = f64(_z) / f64(denom);

  // Find unit cube that contains point
  const X: i32 = i32(Math.floor(x)) & 255;
  const Y: i32 = i32(Math.floor(y)) & 255;
  const Z: i32 = i32(Math.floor(z)) & 255;

  // Find relative x,y,z of point in cube
  x -= Math.floor(x);
  y -= Math.floor(y);
  z -= Math.floor(z);

  // Compute fade curves for each x,y,z
  const u: f64 = fade(x);
  const v: f64 = fade(y);
  const w: f64 = fade(z);

  // Hash coordinates of the 8 cube corners
  const A: i32 = p[X] + Y;
  const AA: i32 = p[A] + Z;
  const AB: i32 = p[A + 1] + Z;
  const B: i32 = p[X + 1] + Y;
  const BA: i32 = p[B] + Z;
  const BB: i32 = p[B + 1] + Z;

  // Add blended results from 8 corners of cube
  const r: f64 = lerp(
    w,
    lerp(
      v,
      lerp(u, grad(p[AA], x, y, z), grad(p[BA], x - 1, y, z)),
      lerp(u, grad(p[AB], x, y - 1, z), grad(p[BB], x - 1, y - 1, z))
    ),
    lerp(
      v,
      lerp(u, grad(p[AA + 1], x, y, z - 1), grad(p[BA + 1], x - 1, y, z - 1)),
      lerp(u, grad(p[AB + 1], x, y - 1, z - 1), grad(p[BB + 1], x - 1, y - 1, z - 1))
    )
  );

  // Shift to range from 0 to 1
  return (r + 1) / 2;
}

function fade(t: f64): f64 {
  return t * t * t * (t * (t * 6 - 15) + 10);
}

function lerp(t: f64, a: f64, b: f64): f64 {
  return a + t * (b - a);
}

function grad(hash: i32, x: f64, y: f64, z: f64): f64 {
  switch (hash & 0xf) {
    case 0x0:
      return x + y;
    case 0x1:
      return -x + y;
    case 0x2:
      return x - y;
    case 0x3:
      return -x - y;
    case 0x4:
      return x + z;
    case 0x5:
      return -x + z;
    case 0x6:
      return x - z;
    case 0x7:
      return -x - z;
    case 0x8:
      return y + z;
    case 0x9:
      return -y + z;
    case 0xa:
      return y - z;
    case 0xb:
      return -y - z;
    case 0xc:
      return y + x;
    case 0xd:
      return -y + z;
    case 0xe:
      return y - x;
    case 0xf:
      return -y - z;
    default:
      return 0; // never happens
  }
}

